  position: absolute;
  display: block;
  font-size: 1em !important;
  content: "";
  width: 0.1em !important;
  left: 0.44em;
  /* half width of clock - half width of hand */
  background: #fff;
  border-radius: 0.5em;
}
.b-icon-clock-live:before {
  top: 0.15em;
  height: 0.4em !important;
  /* half the width/height of the clock */
